@tailwind base;
@tailwind components;
@tailwind utilities;

:root {
    --bg-primary: #8d8484;
    --bg-secondary: #6b6161;
    --bg-accent: rgba(134, 134, 134, 0.47);
    --bg-text: #ffffff;
    --bg-textdark: #b7b7b7;

    --form-bg: #8d7979;
    --form-text: #000;
    --form-border: var(--bg-accent);
}


html * {
    font-family: "Andale Mono", serif !important;
}

html {
    background-color: var(--bg-primary);
    color: var(--bg-text);
}

a {
    color: #5affbd;
}

.window{
    display: flex;
    width: 90vw;
    background-color: var(--bg-secondary);
    border-radius: 5px;
    padding-top: 10px;
    padding-bottom: 40px;
    align-items: center;
    flex-direction: column;
}

.break {
    flex-basis: 100%;
    height:0;
}

.centered {
    height: 110%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding-top: 10px;
    padding-bottom: 10px;
}

.warning {
    text: #ff0000;
}

button{
    height: 30px;
    width: 60px;
    margin: 10px;
    padding-left: 5px;
    justify-content: center;
    border-radius: 5px;
    background-color: var(--bg-primary);
    color: var(--bg-text);
}

button:hover{
    background-color: var(--bg-accent);
    color: var(--bg-textdark);
}


textarea{
    display: block;
    margin-top: 5px;
    margin-left: 12.5%;
    margin-right: 5%;
    width: 800px;
    background-color: var(--form-bg);
    flex-grow: inherit;
    padding: 0;
    border: 1px solid #ccc;
}
